@import url('https://fonts.googleapis.com/css2?family=Comfortaa:wght@300..700&family=Gidugu&display=swap');

*{
  box-sizing: border-box;
  margin: 0;
}

body{
  margin: 0;
  background-color: #06524d;
  color: rgb(255, 255, 255);
  text-shadow:1px 6px 5px #070000;
  font-family:  "Comfortaa", sans-serif;
}

.container{
  text-align: center;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.nav{
  background-color: #29635f;
  color: white;
  display: flex;
  justify-content: space-between;
  gap: 2rem;
  padding: 0 1rem;
  position: fixed;
  left: 0;
  right: 0;
}

.logo{
  height: 80px;
  width: 80px;
}

.nav ul{
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  gap: 1rem;
}

.nav a{
  text-decoration: none;
  height: 100%;
  display: flex;
  align-items: center;
  padding: .25rem ;
  color: white;
  transition:all .4s linear;
  font-size: 20px;
}

.nav li.active a {
  color: #10ecdd;
}

.nav a:hover{
  color: #10ecdd;
}

h1{
  font-size: 150px;
}




